document.addEventListener('DOMContentLoaded', () => {
  const storageKey = 'theme-preference';

  const getColorPreference = () => {
    if (localStorage.getItem(storageKey)) {
      return localStorage.getItem(storageKey);
    } else {
      return window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
    }
  };

  const theme = {
    value: getColorPreference()
  };

  const setPreference = () => {
    localStorage.setItem(storageKey, theme.value);
    reflectPreference();
  };

  const reflectPreference = () => {
    document.documentElement.setAttribute('data-theme', theme.value);
    document.querySelector('.theme-switch') ?.setAttribute('aria-label', theme.value);

    const icons = document.querySelectorAll('.theme-switch__icon');
    if (icons) {
      icons.forEach(icon => icon.classList.remove("theme-switch__icon--active"));
      const activeIcon = document.querySelector(`.theme-switch__icon.${theme.value}`);
      if (activeIcon) {
        activeIcon.classList.add("theme-switch__icon--active");
      }
    }

    const logo = document.querySelector('.logo__img');
    if (logo) {
      logo.src = theme.value === "dark" ? "images/logo.svg" : "images/logo-white.svg";
    }
  };

  const onClick = () => {
    theme.value = theme.value === 'light' ? 'dark' : 'light';
    setPreference();
  };

  reflectPreference();
  document.querySelector('.theme-switch') ?.addEventListener('click', onClick);

  window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', ({
    matches: isDark
  }) => {
    theme.value = isDark ? 'dark' : 'light';
    setPreference();
  });
});
